.winners {
    width: 100%;
    height: fit-content;
    color: white;
    padding-top: 100px;
    &__header {
        display: block;
        font-size: 2.5rem;
        font-weight: 400;
        text-transform: uppercase;
        color: rgb(255, 0, 255);
        letter-spacing: 0.3rem;

    }



    &__table {
        width: 100%;
        border-collapse: collapse;
        margin-top: 15px;
        &__header {
            // border: 2px solid rgb(255, 0, 255);
            // border-radius: 25px;
            // height: 55px;
            text-align: center;

            &__text {
                position: relative;
                padding: 15px 10px;
                text-align: start;
               
                &__wrapper{
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    // gap: 50%;
                    span{
                        letter-spacing: 0.1rem;
                        font-size: var(--font-size-m);
                    }
                    button{
                        z-index: 55;
                    }
                }
               
            }
            &__text::after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                
                // box-shadow: 0px 0px 10px 5px rgb(255, 0, 255);
                border:2px solid rgb(255, 0, 255);
                border-left: 0;
                border-right: 0;
                border-radius: 0;
            }
          

            &__text:first-child {
                position: relative;
                &::after{
                    border-radius: 10px;
                    border: 2px solid rgb(255, 0, 255);
                    // border-top: 0;
                    // border-bottom: 0;
                    border-right-width: 0;
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                }
            }
            &__text:last-child {
                position: relative;
                &::after{
                    border-radius: 10px;
                    border: 2px solid rgb(255, 0, 255);
                    // border-top: 0;
                    // border-bottom: 0;
                    border-left-width: 0;
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }
            }
            
        }

        &__row{
            td{
                padding: 5px 10px;
                font-size: var(--font-size-l);
                color: lightcyan;
            }
        }
    }
    &__footer{
        &__pagination{
            padding-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            &__controls{
                display: flex;
                align-items: center;
                gap: 5px;
                h4{
                    display: block;
                    font-size: var(--font-size-xl);
                    color: rgba(187, 155, 203, 1);
                    text-shadow:2px 2px 15px  rgb(252, 16, 146) ;
                    font-weight: 400;
                }
        
                &__btn{
                    &.disabled{
                        
                        svg{
                            transition: color 0.2s ease-in-out;
                            color: gray;
                        }
                        &:hover{
                            svg{
                                color:gray;
                                filter: none
                            } 
                        }
                    }
                    svg{
                        transition: color 0.2s ease-in-out;
                        color: #A1C6D4;
                    }
                    &:hover{
                        svg{
                            color:#BB9BCB;
                            filter: drop-shadow(4px 5px 15px rgba(252, 16, 146, 0.7));
                        }
                    }
                }
            }
        }
    }
    
}

.highlight{
    color: rgba(187, 155, 203, 1) !important;
    text-shadow: 0px 5px 5px rgba(187, 155, 203, 1) ;
}