.winner{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: rgba(0,0,0,0.5);
    &__banner{
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        padding: 20px 15px;
        width: 400px;
        height: 280px;
        border-radius: 25px;
        box-shadow: 0px 2px 40px 20px rgb(255, 0, 255);
        border: 15px solid rgb(255, 0, 255);
        &__inner{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            box-shadow: 2px 3px 10px 8px rgb(91, 208, 247);
            border: 5px solid rgb(91, 208, 247);
            
            h2{
                text-align: center;
                text-transform: uppercase;
                display: block;
                font-size: var(--font-size-xl);
                font-weight: 400;
                color: greenyellow;
                line-height: 1.4;
                letter-spacing: 0.3rem;
                // user-select: none;
            }
        }
     
    }
}
